import React, { useMemo } from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { FixedSizeList as List, ListOnScrollProps } from 'react-window';
import AutoSizer from 'react-virtualized-auto-sizer';
import { isNil } from 'lodash-es';
import { LoadingBetaNormal, SpinErrorCard } from '@ali/we-design';
import isKeyHotkey from 'is-hotkey';
import { deactiveMention, insertMention } from '../actions';
import MentionSuggestionItem, {
  MentionSuggestionItemProps,
} from './suggestionItem';
import type { Suggestion, MentionPluginConfig, ISearchSuggestionErrData } from '../types';
import { SuggestionsWrapper, LoadingWrapper } from './styled';
import { useDelayedState } from '@ali/4ever-component';

export interface MentionSuggestionProps
  extends Pick<MentionSuggestionItemProps['data'], 'renderItem'>,
  // @ts-ignore
  Omit<MentionPluginConfig<Suggestion>, 'locale' | 'getCurrentUser'> {
  controller: Controller;
  /** 当前的搜索字串 */
  text: string;
}

const isUpHotKey = isKeyHotkey('up');
const isDownHotKey = isKeyHotkey('down');
const isLeftHotKey = isKeyHotkey('left');
const isRightHotKey = isKeyHotkey('right');

const MentionSuggestion: React.FC<MentionSuggestionProps> = (props) => {
  const {
    controller,
    text,
    searchSuggestions,
    renderItem,
    renderHeader,
    renderFooter,
    renderLoading,
    renderEmpty,
    offsetBottom = 0,
    loadMoreSuggestions,
    itemHeight = 50,
    at,
  } = props;

  const [suggestions, setSuggestions] = React.useState<Suggestion[]>([]);
  const [errData, setErrData] = React.useState<ISearchSuggestionErrData>({});
  const [selectedIndex, setSelectedIndex] = React.useState<number>(0);
  const searching = React.useRef(false);
  const [delayedSearching, setDelayedSearching] = useDelayedState(false);

  const listRef = React.useRef<List>(null);
  const lastItem = React.useRef<number | null>(null);
  const editorRef = React.useRef<Controller>(controller);

  const isEmptyText = text.trim().length === 0;
  const isEmptySuggestions = suggestions.length === 0;

  const handleSelect = React.useCallback(
    (index?: number) => {
      const suggestion = suggestions[isNil(index) ? selectedIndex : index];
      editorRef.current.run('onAction', insertMention(suggestion, at));
    },
    [suggestions, selectedIndex],
  );

  const handleScroll = React.useCallback(
    ({ scrollOffset }: ListOnScrollProps) => {
      if (!loadMoreSuggestions || !listRef.current) {
        return;
      }
      /* 当倒数第 offsetBottom + 1 个元素进入可视区域时触发 loadMoreSuggestions */
      const startOffset =
        (suggestions.length - offsetBottom - 2) * itemHeight -
        (listRef.current.props.height as number);

      if (
        startOffset > 0 &&
        startOffset <= scrollOffset &&
        lastItem.current !== suggestions.length
      ) {
        lastItem.current = suggestions.length;

        loadMoreSuggestions(text, suggestions.length, setSuggestions, setErrData);
      }
    },
    [loadMoreSuggestions, suggestions.length, offsetBottom, itemHeight, text],
  );

  const itemData = React.useMemo(
    () => ({
      suggestions,
      onSelect: handleSelect,
      renderItem,
      selectedIndex,
      onSelectedIndexChange: setSelectedIndex,
    }),
    [suggestions, renderItem, selectedIndex, handleSelect],
  );

  React.useEffect(() => {
    editorRef.current = controller;
  }, [controller]);

  // listen up & down
  React.useEffect(() => {
    const handleMove = (event: KeyboardEvent) => {
      if (event.isComposing || searching.current) {
        return;
      }
      if (isRightHotKey(event) || isDownHotKey(event)) {
        event.preventDefault();
        setSelectedIndex((index) => (index + 1) % suggestions.length);
      }
      if (isLeftHotKey(event) || isUpHotKey(event)) {
        event.preventDefault();
        setSelectedIndex((index) => (index === 0 ? suggestions.length - 1 : index - 1));
      }
    };

    document.addEventListener('keydown', handleMove);

    return () => {
      document.removeEventListener('keydown', handleMove);
    };
  }, [suggestions]);

  React.useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (!event.key) {
        return;
      }
      const key = event.key.toLowerCase();
      if (event.isComposing || searching.current) {
        return;
      }
      if (key === 'enter') {
        event.preventDefault();
        event.stopPropagation();
        if (delayedSearching) {
          return;
        }
        handleSelect();
        return editorRef.current.run('onAction', deactiveMention());
      }
      if (key === ' ' && event.keyCode === 32) {
        if (!isEmptyText && !isEmptySuggestions) {
          event.preventDefault();
          event.stopPropagation();
          handleSelect(0);
        } else {
          return editorRef.current.run('onAction', deactiveMention());
        }
      }
    };
    document.addEventListener('keydown', handleKeyDown);
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [handleSelect, isEmptySuggestions, isEmptyText, delayedSearching]);

  // reset loading when search done
  React.useEffect(() => {
    setDelayedSearching(false);
    searching.current = false;
  }, [setDelayedSearching, suggestions]);

  // do search when text changed
  React.useEffect(() => {
    // @ts-ignore value 没有 isComposing 属性
    if (editorRef.current.value.isComposing) {
      return;
    }
    if (!searchSuggestions) {
      // eslint-disable-next-line no-console
      console.warn('Missing `searchSuggestions`');
      return;
    }
    setDelayedSearching(true, 200);
    searching.current = true;
    searchSuggestions(text, setSuggestions, setErrData);
  }, [text, searchSuggestions, setDelayedSearching]);

  // scroll to selected index
  React.useEffect(() => {
    if (listRef.current) {
      listRef.current!.scrollToItem(selectedIndex);
    }
  }, [selectedIndex]);

  return (
    <SuggestionsWrapper>
      {renderHeader && renderHeader()}
      <div style={{ flex: 1 }}>
        <AutoSizer>
          {({ height, width }) => (delayedSearching ? (
            <LoadingWrapper
              data-testid="mention-suggestion-loading"
              height={height}
              width={width}
            >
              {renderLoading ? renderLoading() : <LoadingBetaNormal />}
            </LoadingWrapper>
          ) : (
            <>
              {
                errData.isServiceError ?
                  <SpinErrorCard
                    style={{ width: `${width}px`, height: `${height}px`, border: 'none', }}
                    tip={errData.serviceErrorMsg}
                  />
                  : suggestions.length === 0 && renderEmpty && renderEmpty()
              }
              {
                suggestions.length > 0 &&
                (
                  <List
                    className="mention-suggestion-list"
                    ref={listRef}
                    height={height}
                    itemCount={suggestions.length}
                    itemSize={itemHeight}
                    itemData={itemData}
                    width={width}
                    onScroll={handleScroll}
                  >
                    {MentionSuggestionItem}
                  </List>
                )
              }
            </>
          ))
          }
        </AutoSizer>
      </div>
      {renderFooter && renderFooter()}
    </SuggestionsWrapper>
  );
};

export default React.memo(MentionSuggestion);
